






$utilities: map-merge(
$utilities,(
"font-size": map-merge(
      map-get($utilities,"font-size"),
      (
        responsive: true,
      )
    ),
"rounded":  (
      property: border-radius,
      class: rounded,
      values: (
        null: $border-radius,
        0: 0,
        1: $border-radius-sm,
        2: $border-radius,
        3: $border-radius-lg,
        4: $border-radius-xl,
        circle: 50%,
        pill: $border-radius-pill
        )
      ),

"width": map-merge(
        map-get($utilities,"width"),
        (
          responsive: true,
        )
      ),

 "height": map-merge(
        map-get($utilities,"height"),(
          responsive: true,
          values: (
            117: 117px,
            207: 207px,
            210: 210px,
            266: 266px,
            444: 444px,
            25: 25%,
            50: 50%,
            75: 75%,
            100: 100%,
            auto: auto
          )
        )
      ),
    


  )

);

.object-fit-cover {
  object-fit: cover;
}



